import {Component,Input,OnChanges,OnInit,Injectable,Output,EventEmitter} from "@angular/core";
import { NgModule } from '@angular/core';
import {ButtonModule} from 'primeng/primeng';


@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.css'],
  providers: [],
})
@NgModule({
   imports: [
     ButtonModule
   ],
   declarations: [
     Component
   ],
   bootstrap: [ Component ]
})

@Injectable()
export class FilterComponent implements OnInit,OnChanges {
  tiketquantity = 1;
  seatPrices: number[] = [0,50000];//範囲
  seatName: string = "";//席名検索
  seatValues:any[] = [true,true];
  numberflag: boolean = false;
  priceflag: boolean = false;
  seatflag: boolean = false;
  clearflag:boolean = false;

  numberClick() {
    this.priceflag = false;
    this.seatflag = false;
      if(this.numberflag == true){
        this.numberflag = false;
      }else{
        this.numberflag = true;
      }
  }
  priceClick() {
    this.numberflag = false;
    this.seatflag = false;
      if(this.priceflag == true){
        this.priceflag = false;
      }else{
        this.priceflag = true;
      }
  }
  seatClick() {
    this.numberflag = false;
    this.priceflag = false;
      if(this.seatflag == true){
        this.seatflag = false;
      }else{
        this.seatflag = true;
      } 
  }
  clearClick(){
    this.clearflag = true;
    setInterval(() => {
    this.clearflag = false;
    }, 2000);
  }

  constructor() { 
  }
  onTiketNumOut(num): void {
    this.tiketquantity = num;
  }
  onRangeValuesOut(nums): void {
    this.seatPrices = nums;
  }
  onSeatTextOut(str): void {
    this.seatName = str;
  }
  onSeatValuesOut(anys): void {
    this.seatValues = anys;
  }
  ngOnInit() {
  }
  ngOnChanges() {
　}
}



