import { Component,NgModule, OnInit } from '@angular/core';
import {ButtonModule,DropdownModule,SelectItem} from 'primeng/primeng';

@Component({
  selector: 'app-select-product',
  templateUrl: './select-product.component.html',
  styleUrls: ['./select-product.component.css']
})
@NgModule({
   imports: [
     ButtonModule,
     DropdownModule,
   ],
   declarations: [
     Component
   ],
   bootstrap: [ Component ]
})
export class SelectProductComponent implements OnInit {
  /*画像URL*/
  venueURL = './assets/for_thumbnail_all.svg'; 
  //公演ID
  performanceId: number = 1;
  
  /*席番号*/
  seatinfoA:string = "C列6番";
  seatinfoB:string = "C列7番";
  /*席料金*/
  seatprices: SelectItem[];
  selectedA: string;
  selectedB: string;
  constructor() {
    this.seatprices = [];
        this.seatprices.push({label:'大人　5,000円 + 手数料', value:{id:1, name: '大人', code: '01'}});
        this.seatprices.push({label:'小人　4,000円 + 手数料', value:{id:2, name: '子供', code: '02'}});
   }

  ngOnInit() {
  }
}



