/*
api全てのget,post処理を行うサービス
*/
import { Injectable }  from '@angular/core';
import { Http, XHRBackend, RequestOptions, Request, RequestOptionsArgs, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { ISuccessResponse, IErrorResponse } from './interfaces';

@Injectable()
export class ApiBase extends Http{


  constructor(backend: XHRBackend,
              options: RequestOptions) {

      super(backend, options);
      this.settingHeader(options);
  }

  /**
   * APIリクエスト時の共通ヘッダーを設定します
   *
   * @param RequestOptions options - リクエストオプション
   */
  protected settingHeader(options: RequestOptions) {
  }


  /**
   * GETリクエストを実行します
   *
   * @param string url - API-URL
   * @return Observable<T> - Observable関数
   * @return null - 通信エラー
   */
  protected httpGet<T>(url: string): Observable<T> {
    console.debug('httpGet', url);
    return this.get(url, this.options)
      .map((response) => response.json())
      .catch((error) => Observable.throw(
        (error.message === 'Timeout has occurred') ? null : error.json())
      );
  }

  /**
   * POSTリクエストを実行します
   *
   * @param string url - API-URL
   * @param Object data - ポストデータ
   * @return Observable<T> - Observable関数
   * @return null - 通信エラー
   */
  protected httpPost<T>(url: string, data?: {}): Observable<T> {
    console.debug('httpPost', url);
    return this.post(url, data, this.options)
      .map((response) => response.json())
      .catch((error) => Observable.throw(
        (error.message === 'Timeout has occurred') ? null : error.json())
      );
  }

}



