/*以下のAPIを呼び出し情報の取得管理を行うサービス（Injectable）。
・公演情報検索　http://dev.altair-spa.tk/cart_api/api/v1/events/{eventId}/performances
・公演情報　　　http://dev.altair-spa.tk/cart_api/api/v1/performances/{performanceId}
*/
import { Injectable, EventEmitter } from '@angular/core';
import { XHRBackend, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { ApiBase }  from './api-base.service';
import { ISuccessResponse, IErrorResponse, IPerformanceInfoResponse, IPerformancesResponse, IPerformance}  from './interfaces';
import { ApiConst } from '../../app.constants';

@Injectable()
export class PerformancesService extends ApiBase{

  public performanceLoaded$: EventEmitter<IPerformance>;
  private performanceInfo: IPerformanceInfoResponse;
  private performance: IPerformance;

  constructor(backend: XHRBackend,
              options: RequestOptions) {
    super(backend, options);
    this.performanceLoaded$ = new EventEmitter<IPerformance>();
  }
  /**
   * 公演情報取得
   * @param {number} performanceId 公演ID
   * @return {Observable} see http.get()
   */
  getPerformance(performanceId: number): Observable<IPerformanceInfoResponse | IErrorResponse> {

    //キャッシュがあれば返す
    if(this.performanceInfo && this.performanceInfo.data.performance.performance_id == performanceId){
        this.performanceLoaded$.emit(this.performance);
        return Observable.of(this.performanceInfo);
    }

    const url = `${ApiConst.API_URL.PERFORMANCE_INfO.replace(/{:performance_id}/, performanceId + '')}`;
    var get = this.httpGet(url).map((response: IPerformanceInfoResponse) => {
      response.data.performance.sales_segments = response.data.sales_segments;
      return response;
    });
    get.subscribe((response: IPerformanceInfoResponse) => {
      this.performanceInfo = response;
      this.performance = response.data.performance;
      this.performanceLoaded$.emit(this.performance);
    });
    return get;
  }

  /**
   * 公演情報検索
   * @param  {number}     eventId イベントID
   * @return {Observable} see http.get()
   */
  findPerformancesByEventId(eventId: number): Observable<IPerformancesResponse | IErrorResponse> {
    const url = `${ApiConst.API_URL.PERFORMANCES.replace(/{:event_id}/, eventId + '')}`;
    var get = this.httpGet(url);
    get.subscribe((response: IPerformancesResponse) => {
    });
    return this.httpGet(url);
  }
}


