import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule,JsonpModule} from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import {HashLocationStrategy, Location, LocationStrategy} from '@angular/common';

import { AppComponent } from './app.component';
import { ReserveBySeatComponent } from './reserve-by-seat/reserve-by-seat.component';
import { PaymentComponent } from './payment/payment.component';
import { ReserveByQuantityComponent } from './reserve-by-quantity/reserve-by-quantity.component';
import { SelectProductComponent } from './select-product/select-product.component';

import { EventinfoComponent } from './reserve-by-seat/event-info/event-info.component';
import { FilterComponent } from './reserve-by-seat/filter/filter.component';
import { VenuemapComponent } from './reserve-by-seat/venue-map/venue-map.component';
import { SeatlistComponent } from './reserve-by-seat/seat-list/seat-list.component';
import { SearchbarComponent } from './reserve-by-seat/filter/search-bar/search-bar.component';
import { PageNotFoundComponent } from './errors/page-not-found/page-not-found.component';

import 'hammerjs' 

//サービス
import { ApiBase } from './shared/services/api-base.service'
import { PerformancesService } from './shared/services/performances.service';
import { SeatInformationManagementService } from './shared/services/seat-information-management.service';
import { SeatStatusService } from './shared/services/seat-status.service';
import { StockTypesService } from './shared/services/stock-types.service';
import { SeatsService } from './shared/services/seats.service';

//ng-inline-svg
import { InlineSVGModule } from 'ng-inline-svg';
//primeng
import {InputTextModule,DataTableModule,ButtonModule,DialogModule,SliderModule,CheckboxModule,DropdownModule} from 'primeng/primeng';
//loding-animation
import { LoadingAnimateModule, LoadingAnimateService } from 'ng2-loading-animate';


const routes: Routes = [
  { path: 'performances/:performance_id', component: ReserveBySeatComponent },
  { path: 'performances/:performance_id/reserve-by-quantity/:stock_type_id', component: ReserveByQuantityComponent },
  { path: 'performances/:performance_id/select-product', component: SelectProductComponent },
  { path: 'payment/', component: PaymentComponent },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    EventinfoComponent,
    FilterComponent,
    VenuemapComponent,
    SeatlistComponent,
    SearchbarComponent,
    ReserveBySeatComponent,
    PaymentComponent,
    ReserveByQuantityComponent,
    SelectProductComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    InlineSVGModule,
    BrowserModule,FormsModule,HttpModule,InputTextModule,DataTableModule,ButtonModule,DialogModule,SliderModule,CheckboxModule,DropdownModule,
    LoadingAnimateModule.forRoot(),
    RouterModule.forRoot(routes)
  ],
  providers: [
              Location, {provide: LocationStrategy, useClass: HashLocationStrategy},
              LoadingAnimateService, ApiBase, SeatInformationManagementService, SeatStatusService, StockTypesService, PerformancesService, SeatsService
  ],
  bootstrap: [
              AppComponent
  ]
})
export class AppModule { }



