import { Component, Input, OnInit, AfterContentInit, AfterContentChecked ,AfterViewChecked ,OnChanges} from '@angular/core';
import { NgModule } from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FilterComponent } from '../../reserve-by-seat/filter/filter.component';
import { VenuemapComponent } from '../../reserve-by-seat/venue-map/venue-map.component';
import { PerformancesService } from '../../shared/services/performances.service';
import { SeatsService } from '../../shared/services/seats.service';
import { StockTypesService } from '../../shared/services/stock-types.service';
import { IPerformance,
         ISeatsResponse,IStockType,
         IStockTypeResponse, IStockTypesResponse,} from '../../shared/services/interfaces';
import { ActivatedRoute,Router } from '@angular/router';
@Component({
  providers: [FilterComponent, VenuemapComponent],
  selector: 'app-seat-list',
  templateUrl: './seat-list.component.html',
  styleUrls: ['./seat-list.component.css']
})
export class SeatlistComponent implements OnInit {
  @Input() private filterComponent: FilterComponent;
  @Input() private venuemapComponent: VenuemapComponent;
  //検索条件
  // tiketquantity: number = 0;
  // tiketSeatPriceMin: number = 0;
  // tiketSeatPriceMax: number = 50000;
  // tiketSeatName: string = "";
  // tiketSeatType: number = 3;
  // ticketData: any;
  performance: IPerformance;
  performanceId: number;
  seat:IStockType[];
  
  constructor(private performances: PerformancesService,private seats: SeatsService,
              private route: ActivatedRoute,private stockTypes: StockTypesService) {
  }
  readFlag(){
    if (this.filterComponent.numberflag || this.filterComponent.priceflag || 
        this.filterComponent.seatflag || !this.venuemapComponent.hideSeatlist) {
          return true;
    }else{
          return false;
    }
  }
  ngAfterViewChecked(){
  }
  ngAfterContentChecked	(){
    
  }
  ngAfterViewInit() {
    // setInterval(() => {
    //   this.readTiketQuantity();
    //   this.readTiketSeatPriceMin();
    //   this.readTiketSeatPriceMax();
    //   this.readTiketSeatName();
    //   this.readTiketSeatType();
    // }, 1000);
  }
  ngOnChanges() {
    
　}
  ngOnInit() {
    //公演情報取得
    this.performances.performanceLoaded$.subscribe((performance: IPerformance) => {
      this.performance = performance;
    });

    this.route.params.subscribe((params) => {
      if(params && params['performance_id']){
        //パラメーター切り出し
        this.performanceId = +params['performance_id'];
        //席種情報検索取得
        this.stockTypes.findStockTypesByPerformanceId(this.performanceId).subscribe((response: IStockTypesResponse) => {
          console.log(`get stock_types(#${this.performanceId}) success`, response);
          this.seat = response.data.stock_types;
        },
        (error) => {
          console.log('seats error', error);
          alert('エラー：座席情報検索apiを取得できません');
        });
      }else{
        alert('エラー：公演IDを指定してください');
        console.error("エラー:公演IDを取得できません");
      }
    });
    console.log(this.seat);
  } 
};


