/*以下のAPIを呼び出し情報の取得管理を行うサービス（Injectable）。
・座席情報検索　http://dev.altair-spa.tk/cart_api/api/v1/performances/{performanceId}/seats
*/
import { Injectable, EventEmitter } from '@angular/core';
import { XHRBackend, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { ApiBase }  from './api-base.service';
import { ISuccessResponse, IErrorResponse, ISeatsResponse}  from './interfaces';
import { ApiConst } from '../../app.constants';

@Injectable()
export class SeatsService extends ApiBase{

  constructor(backend: XHRBackend,
              options: RequestOptions) {
    super(backend, options);
  }

  /**
   * 座席情報検索
   * @param  {number}     eventId イベントID
   * @return {Observable} see http.get()
   */
  findSeatsByPerformanceId(performanceId: number): Observable<ISeatsResponse | IErrorResponse> {
    const url = `${ApiConst.API_URL.SEATS.replace(/{:performance_id}/, performanceId + '')}`;
    return this.httpGet(url);
  }
}


