import {Component, Input, OnInit, OnChanges,Output,EventEmitter,AfterViewInit} from '@angular/core';
import { NgModule } from '@angular/core';
import { FilterComponent } from '../filter.component';

//primeng
import {SliderModule,ButtonModule,InputTextModule,CheckboxModule} from 'primeng/primeng';
import * as $ from "jquery";

@Component({
  selector: 'search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.css'],
  providers: [FilterComponent],
})
@NgModule({
  imports: [
    SliderModule,
    ButtonModule,
    InputTextModule,
    CheckboxModule
  ],
  declarations: [
    Component
  ],
  bootstrap: [ Component ]
})
export class SearchbarComponent implements OnInit,OnChanges,AfterViewInit{
  /*チケット枚数*/
  tiketNum: number = 1;//初期値
  /*チケット金額*/
  minValue: number = 0;//最小枚数
  maxValue: number = 50000;//最大枚数
  rangeValues: number[] = [this.minValue,this.maxValue];//範囲
  /*席種（検索）*/
  seatText: string = "";
  /*席種（自由席・指定席）*/
  unreserved:boolean = true;//自由席 
  reserved:boolean = true;//指定席
  
  //親からパラメータをinput
  @Input() numberflag: boolean;
  @Input() priceflag: boolean;
  @Input() seatflag: boolean;
  @Input() clearflag: boolean;
  //親へパラメータをoutput
  @Output() tiketNumOut = new EventEmitter();
  @Output() rangeValuesOut = new EventEmitter();
  @Output() seatTextOut = new EventEmitter();
  @Output() seatValuesOut = new EventEmitter();
  constructor(private FilterComponent: FilterComponent) {
    
  }
  
  minusClick(){
    if(this.tiketNum > 1){
      this.tiketNum--;
    }
    this.tiketNumOut.emit(this.tiketNum);
  }
  plusClick(){
    this.tiketNum++;
    this.tiketNumOut.emit(this.tiketNum);
  }

   /*スライダー変更時の処理*/
  handleChange(e) {
     this.minValue = this.rangeValues[0];
     this.maxValue = this.rangeValues[1];
     this.rangeValuesOut.emit(this.rangeValues);
  }

  ngAfterViewInit() {
    setInterval(() => {
    //テキストの変更監視
    this.seatTextOut.emit(this.seatText);
    //自由席・指定席変更監視
    var seatTypeValues:any[] = [this.unreserved,this.reserved];
    this.seatValuesOut.emit(seatTypeValues);
    }, 1000);
  }
  ngOnInit() {
  }
  ngOnChanges() {
    if(this.clearflag){
      this.tiketNum = 1;
      this.minValue = 0;
      this.maxValue = 50000;
      this.seatText = "";
      this.unreserved = true;
      this.reserved = true;
      this.tiketNumOut.emit(this.tiketNum);
      this.rangeValuesOut.emit(this.rangeValues);
      this.seatTextOut.emit(this.seatText);
      //this.seatValuesOut.emit(this.seatTypeValues);
    }
　}
}



