import { Component, NgModule, OnInit } from '@angular/core';
import { ButtonModule } from 'primeng/primeng';
import { PerformancesService } from '../shared/services/performances.service';
import { StockTypesService } from '../shared/services/stock-types.service';
import { SeatStatusService } from '../shared/services/seat-status.service';
import { IPerformance, IPerformanceInfoResponse,
         IStockTypeResponse, IStockTypesResponse, IStockType,
         ISeatsReserveResponse,ISeatsReleaseResponse,IResult,
         ISeatsResponse
       } from '../shared/services/interfaces';
import { ActivatedRoute,Router } from '@angular/router';
import { SeatsService } from '../shared/services/seats.service';


@Component({
  selector: 'app-reserve-by-quantity',
  templateUrl: './reserve-by-quantity.component.html',
  styleUrls: ['./reserve-by-quantity.component.css']
})
@NgModule({
   imports: [
     ButtonModule
   ],
   declarations: [
     Component
   ],
   bootstrap: [ Component ]
})
export class ReserveByQuantityComponent implements OnInit {

  //ページタイトル
  pageTitle: string;

  //画像URL
  venueURL = './assets/for_thumbnail_all.svg'; 
  
  //チケット枚数
  quantity: number = 1;//初期値
  
  //席種ID
  stockTypeId: number;
  
  //公演ID
  performanceId: number;
  
  //公演情報
  performance: IPerformance;
  
  //席種
  stockType: IStockType;
  
  //席種名
  stockTypeName:string;
  
  //商品配列[]
  products:any;
  
  //枚数選択POST初期データ
  data: {} = {
              "reserve_type": "auto",
              "auto_select_conditions": {
                "stock_type_id": 0,
                "quantity": 0
              }
             }

  //座席確保ステータス
  seatPostStatus:string;
  

  constructor(private route: ActivatedRoute,private router:Router,
              private performances: PerformancesService, private stockTypes: StockTypesService,
              private seatStatus: SeatStatusService,private seats: SeatsService) {
  }

  ngOnInit() {
      console.log("OnInit reserve-by-quantity");
      this.loadPerformance();
      //席種情報取得処理
      var that = this;
      this.stockTypes.stockTypeLoaded$.subscribe((stockType: IStockType) => {
      that.stockTypeName = stockType.stock_type_name;
      that.products = stockType.products;
    });
  }

  private loadPerformance(){
    this.route.params.subscribe((params) => {
      if(params && params['performance_id']){
        //パラメーター切り出し
        this.performanceId = +params['performance_id'];
        this.stockTypeId = +params['stock_type_id'];
        //公演情報取得
        this.performances.getPerformance(this.performanceId)
        .subscribe((response: IPerformanceInfoResponse) => {
          console.log(`get performance(#${this.performanceId}) success`, response);
          this.performance = response.data.performance;
          this.pageTitle = this.performance.performance_name;
        },
        (error) => {
          console.log('performances error', error);
          alert('エラー：公演情報を取得できません');
        });
        //席種情報取得
        this.stockTypes.getStockType(this.performanceId,this.stockTypeId)
        .subscribe((response: IStockTypeResponse) => {
          console.log(`get stockType(#${this.performanceId}) success`, response);
          this.stockType = response.data.stock_type;
        },
        (error) => {
          console.log('stockType error', error);
          alert('エラー：席種情報を取得できません');
        });
      }
      else{
        alert('エラー：公演IDを指定してください');
        console.error("エラー:公演IDを取得できません");
      }
    });
  }
  //チケット枚数減少
  minusClick(){
    if(this.quantity > 1){
      this.quantity--;
    }
  }
  //チケット枚数増加
  plusClick(){
    this.quantity++;
  }
  //座席確保ボタン選択
  seatReserveClick(){
    //dataのquentity,stockTypeId更新
    this.dataUpdate();
    
    this.route.params.subscribe((params) => {
      if(params && params['performance_id']){
        //パラメーター切り出し
        this.performanceId = +params['performance_id'];
        //座席確保api
        this.seatStatus.seatReserve(this.performanceId,this.data).subscribe((response: ISeatsReserveResponse) => {
          console.log(`get seatReserve(#${this.performanceId}) success`, response);
          /*ステータスを取得してNGだったら座席情報検索apiを呼び、空席情報を更新する処理：要追加
          this.seatPostStatus = response.data.results.status;
          */
        },
        (error) => {
          console.log('seatReserve error', error);
          alert('エラー：座席確保apiを取得できません');
        });
      }
      else{
        alert('エラー：公演IDを指定してください');
        console.error("エラー:公演IDを取得できません");
      }
    });
    //@todo レスポンスからステータスを取得して、画面上の席情報の在庫情報を最新化する
    // if(this.seatPostStatus == "OK"){
    //   //OKの場合、商品選択へ画面遷移
      this.router.navigate(["performances/" + this.performanceId + '/select-product/']);
    // }else{
    //NGの場合、座席情報検索apiを呼び、空席情報を更新する処理
        //座席情報検索api
        // this.seats.findSeatsByPerformanceId(this.performanceId).subscribe((response: ISeatsResponse) => {
        //   console.log(`get seats(#${this.performanceId}) success`, response);
        // },
        // (error) => {
        //   console.log('seats error', error);
        //   alert('エラー：座席情報検索apiを取得できません');
        // });
    //alert("席確保に失敗しました。");

    // }
  }
  
  //dataのquentity,stockTypeId更新関数
  dataUpdate(){
    this.data = {
                  "reserve_type": "auto",
                  "auto_select_conditions": {
                      "stock_type_id": this.stockTypeId,
                      "quantity": this.quantity
                  }
                }
  }
}



