import { Component, OnInit, AfterViewInit } from '@angular/core';
import { NgModule } from '@angular/core';
import { PerformancesService } from '../../shared/services/performances.service';
import { IPerformance } from '../../shared/services/interfaces';
//primeng
import {DialogModule,ButtonModule} from 'primeng/primeng';

@Component({
  selector: 'app-event-info',
  templateUrl: './event-info.component.html',
  styleUrls: ['./event-info.component.css']
})
@NgModule({
   imports: [
     DialogModule,ButtonModule
   ],
   declarations: [
     Component
   ],
   bootstrap: [ Component ]
 })


export class EventinfoComponent implements OnInit {

  //会場名
  private venueName: string;

  //開場日時
  private startOn: Date;

  //販売開始日時
  private salesStartAt: Date;

  //販売終了日時
  private salesEndAt: Date;

  //イベント詳細ダイアログ表示状態フラグ（表示・非表示）
  private showEventDetail: boolean = false;
 
  /**
   * コンストラクタ
   */
  constructor(private performances: PerformancesService) {
  }

  /**
   * 初期化処理
   */
  ngOnInit() {
    this.performances.performanceLoaded$.subscribe((performance: IPerformance) => {
      console.log("show event-info");
      console.log("venue name:" + performance.venue_name);
      this.venueName = performance.venue_name;
      this.startOn = new Date(performance.start_on);
      this.salesStartAt = new Date(performance.sales_segments[0].start_at);
      this.salesEndAt = new Date(performance.sales_segments[0].end_at);
    });
  }

  // private onPerformanceLoaded(){

  // }

  /**
   * イベント詳細ダイアログ表示
   */
  showDialog() {
    this.showEventDetail = true;
  }
  
  ngAfterViewInit(){
  }

  onClick(){

  }
}



