import { Component, ElementRef, AfterContentInit, AfterViewInit, OnInit } from '@angular/core';
import { NgModule } from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { InlineSVGModule } from 'ng-inline-svg';

import { StockTypesService } from '../../shared/services/stock-types.service';
import { IPerformance, IPerformanceInfoResponse,
         IStockType,IStockTypeResponse,IStockTypesResponse,IProducts,
         ISeatsReserveResponse,ISeatsReleaseResponse,IResult 
       } from '../../shared/services/interfaces';
import { ActivatedRoute } from '@angular/router';
import { PerformancesService } from '../../shared/services/performances.service';
import { SeatStatusService } from '../../shared/services/seat-status.service';

import * as $ from 'jquery';
import 'hammerjs';
import 'hammer-timejs';
import 'jquery-hammerjs'; 

import { DialogModule } from 'primeng/primeng';

@Component({
  providers: [],
  selector: 'app-venue-map',
  templateUrl: './venue-map.component.html',
  styleUrls: ['./venue-map.component.css'],
})

export class VenuemapComponent implements OnInit   {
  
  constructor(private el: ElementRef,
              private stockTypes: StockTypesService, 
              private route: ActivatedRoute,
              private performances: PerformancesService,
              private seatStatus: SeatStatusService){
  }
  
  //公演ID
  performanceId: number;
  
  //席種
  stockType: IStockType;

  //席種名
  stockTypeName:string;
  
  //席種ID(仮に1を入れる。座席選択処理でIDを取得し、この変数へ入れてください>>桜井さん)
  stockTypeId:number = 1;

  //商品配列[]
  products:any;


    
  venueURL = './assets/rakuten-kobo-stadium-2016-flattened-rev1.svg';
  wholemapURL = './assets/rakuten-kobo-stadium-2016-small1.svg';
  // rect = document.createElementNS("wholemapURL", "rect");
  displayDetail: boolean = false;
  ticketDetail: boolean = false;
  hideSeatlist: boolean = false;
  wholemapFlag: boolean = false;

  fromAddList: boolean = false;
  selectedSeatId: string = null;
  selectedStockType: any = null;
  selectedSeatColor: any = null;
  selectedSeatList: string[] = [];
  countSelect: number = 0;

  ngOnInit() {
    this.loadStockType();
    //席種情報取得処理
    var that = this;
    this.stockTypes.stockTypeLoaded$.subscribe((stockType: IStockType) => {
      that.stockTypeName = stockType.stock_type_name;
      that.products = stockType.products;
    });
    
  }

  private loadStockType(){
    this.route.params.subscribe((params) => {
      if(params && params['performance_id']){
        //パラメーター取得
        this.performanceId = +params['performance_id'];
        //席種情報検索取得
        this.stockTypes.findStockTypesByPerformanceId(this.performanceId)
        .subscribe((response:IStockTypesResponse) => {
          console.log(`get stockTypes(#${this.performanceId}) success`, response);
        }),
        //席種情報取得（ここの処理は座席を選択した時に行う：選択処理が完成後移動）
        this.stockTypes.getStockType(this.performanceId,this.stockTypeId)
        .subscribe((response: IStockTypeResponse) => {
          console.log(`get stockType(#${this.performanceId}) success`, response);
          this.stockType = response.data.stock_type;
        },
        (error) => {
          console.log('stockType error', error);
          alert('エラー：席種情報を取得できません');
        });
        // //座席確保api取得（ここの処理は「次へボタン」を選択した時に行う：処理が完成後移動）
        // this.seatStatus.seatReserve(this.performanceId).subscribe((response: ISeatsReserveResponse) => {
        //   console.log(`get seatReserve(#${this.performanceId}) success`, response);
        // },
        // (error) => {
        //   console.log('seatReserve error', error);
        //   alert('エラー：座席確保apiを取得できません');
        // });
        // //座席解放api取得（ここの処理は「キャンセル」を選択した時に行う：処理が完成後移動「」）
        // this.seatStatus.seatRelease(this.performanceId).subscribe((response: ISeatsReleaseResponse) => {
        //   console.log(`get seatReserve(#${this.performanceId}) success`, response);
        // },
        // (error) => {
        //   console.log('seatReserve error', error);
        //   alert('エラー：座席確保apiを取得できません');
        // });
      }else{
        alert('エラー：公演IDを指定してください');
        console.error("エラー:公演IDを取得できません");
      }
    });
  }

  ngAfterViewInit() {
    var scaleTotal = 1.0;
    var blockFlag = false;
    var prevColor = null;
    var prevId = null;
    const PINK = 'rgb(236, 13, 80)';

    // 座席選択時の色変化
    $('#venue').on('click', '.seat', (e) => {
      if ($(e.target).css('fill') != PINK) {
        this.selectedSeatId = $(e.target).attr('id');
        this.selectedSeatColor = $(e.target).css('fill');
        $(e.target).css({
          'fill': PINK //click時の図形の色
        });
        if (this.countSelect == 0){
          prevColor = this.selectedSeatColor;
          prevId = this.selectedSeatId;
        } else {
          if ((this.selectedSeatId != prevId) && (!this.fromAddList)){
            $('#'+prevId, '#venue').css({
              'fill': prevColor
            });
            prevColor = this.selectedSeatColor;
            prevId = this.selectedSeatId;
          }
        }
        if (this.selectedStockType != this.selectedSeatId.split("-", 1)) { 
          this.selectedStockType = this.selectedSeatId.split("-", 1);
          this.displayDetail = true;
          console.log(this.selectedStockType);
        }
      } 
    });
    // ダブルタップ・ダブルクリックの処理（拡大）
    $('#venue').data("dblTap", false).click((e) => {
	    if($(this).data("dblTap")){
        var viewBoxVals 
        var scale = 0.5; // 1辺の長さの拡大率
        var x = e.offsetX;
        var y = e.offsetY;
        scaleTotal *= scale;
        viewBoxVals = getZoomViewBox(x, y, scale);
        $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // viewBoxを設定する
        if (scaleTotal <= 0.5 && !(this.wholemapFlag)) {
          this.hideSeatlist = true;
          this.wholemapFlag = true;
        }
        if (scaleTotal <= 0.125 && !(blockFlag)) {
          blockFlag = onoffBlock(blockFlag);
        }
		    $(this).data("dblTap", false);
	    } else {
		    $(this).data("dblTap", true);
	    }
	    setTimeout(() => {
		    $(this).data("dblTap", false);
	    }, 500);
    })
    // 個席表示/非表示処理
    function onoffBlock(blockFlag: boolean): boolean {
      if (blockFlag) {
        $('#layer2', '#venue').css({
            'display': 'inline',
          });
        return false;
      } else {
        $('#layer2', '#venue').css({
            'display': 'none',
          });
        return true;
      }
    }
    // 右クリックの処理（縮小）
    $(document).on('contextmenu', (e) => {
      var viewBoxVals; 
      var scale = 2.0; // 1辺の長さの拡大率
      var x = e.pageX;
      var y = e.pageY;
      scaleTotal *= scale;
      viewBoxVals = getZoomViewBox(x, y, scale);
      $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // viewBoxを設定する
      if (scaleTotal > 0.125 && blockFlag) {
        blockFlag = onoffBlock(blockFlag);
      }
      if (scaleTotal > 0.5 && this.wholemapFlag) {
        this.hideSeatlist = false;
        this.wholemapFlag = false;
      }
      return false; // menuを消去
    });
    // マウスホイールの移動量取得
    function extractDelta(e) {
      if (e.wheelDelta) {
          return e.wheelDelta;
      }
      if (e.originalEvent.detail) {
          return e.originalEvent.detail * -40;
      }
      if (e.originalEvent && e.originalEvent.wheelDelta) {
          return e.originalEvent.wheelDelta;
      }
    }
    // マウスホイールによる拡大・縮小
    $('#venue').bind('mousewheel DOMMouseScroll', (e) => {
      var viewBoxVals;
      var x = e.offsetX;
      var y = e.offsetY;
      var scale;
      var d = extractDelta(e);
      if(d > 0) {
          scale = 0.8;
          scaleTotal *= scale;
          viewBoxVals = getZoomViewBox(x, y, scale);
          $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // viewBoxを設定する
          if (scaleTotal <= 0.5 && !(this.wholemapFlag)) {
            this.hideSeatlist = true;
            this.wholemapFlag = true;
          }
          if (scaleTotal <= 0.125 && !(blockFlag)) {
            blockFlag = onoffBlock(blockFlag);
          }
          
      } else {
          scale = 1.2;
          scaleTotal *= scale;
          viewBoxVals = getZoomViewBox(x, y, scale);
          $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // viewBoxを設定する
          if (scaleTotal > 0.125 && blockFlag) {
            blockFlag = onoffBlock(blockFlag);
          }
          if (scaleTotal > 0.5 && this.wholemapFlag) {
            this.hideSeatlist = false;
            this.wholemapFlag = false;
          }
      }
      e.stopPropagation();
      e.preventDefault();
    });
    // 現在のviewBoxの値を取得
    function getPresentViewBox(): any {
      var viewBox = $("#venue").children().attr('viewBox');
      return viewBox.split(' ');
    }
    // 現在の画像width/表示窓widthの比
    function getPresentRatioW(viewBoxValues: any): number {
      var width = $("#venue").children().css('width'); // 表示窓のwidth（px）
      var wid = parseInt(width, 10); // 表示窓のwidth　数値
      var ratioW = parseInt(viewBoxValues[2])/wid; // 拡大前の 画像width/表示窓width
      return ratioW;
    }
    // 現在の画像height/表示窓heightの比
    function getPresentRatioH(viewBoxValues: any): number {
      var height = $("#venue").children().css('height'); // 表示窓のheight（px）
      var hei = parseInt(height, 10); // 表示窓のheight　数値
      var ratioH = parseInt(viewBoxValues[3])/hei; // 拡大前の 画像height/表示窓height
      return ratioH;
    }
    // 拡大・縮小後ののviewBoxの値を取得
    function getZoomViewBox(x: number, y: number, scale: number): any {
      var viewBoxValues = getPresentViewBox();
      var viewBoxVals =[];
      var ratioW = getPresentRatioW(viewBoxValues); // 拡大前の 画像width/表示窓width
      var ratioH = getPresentRatioH(viewBoxValues); // 拡大前の 画像height/表示窓height
      viewBoxVals[2] = scale*parseInt(viewBoxValues[2]); // 拡大後のwidth 
      viewBoxVals[3] = scale*parseInt(viewBoxValues[3]); // 拡大後のheight
      // 拡大前と後でダブルクリックした点が表示窓上の同じ点になるようにviewBoxの始点を求める
      // （拡大前）－（拡大後）の差が拡大後の始点x, y　
      viewBoxVals[0] = (x*ratioW + parseInt(viewBoxValues[0])) - (viewBoxVals[2]/(parseInt(viewBoxValues[2])/ratioW)*x);
      viewBoxVals[1] = (y*ratioH + parseInt(viewBoxValues[1])) - (viewBoxVals[3]/(parseInt(viewBoxValues[3])/ratioH)*y);
      return viewBoxVals;
    }
    // キー操作（移動）
    $(document).on('keydown', (e) => {
      var viewBoxVals 
      var panRate = 50;	// Number of pixels to pan per key press.   
      viewBoxVals = getPanViewBox(e, panRate);
      $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // set the viewBox
    });
    // スワイプ操作（移動）
    $(function() {
      $(document).find('#venue').on('touchstart', onTouchStart); //指が触れたか検知
      $(document).find('#venue').on('touchmove', onTouchMove); //指が動いたか検知
      $(document).find('#venue').on('touchend', onTouchEnd); //指が動いたか検知
      var originalX, originalY, x, y, viewBoxVals;
      var swipeFlag = false;
      //スワイプ開始時の座標を格納
      function onTouchStart(e) {
          swipeFlag = true;
          originalX = getPositionX(e);
          originalY = getPositionY(e);
      }
      //スワイプの距離を取得
      function onTouchMove(e) {
        if (swipeFlag) {
          x = originalX - getPositionX(e);
          y = originalY - getPositionY(e);
          viewBoxVals = getSwipeViewBox(x, y);
          $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // set the viewBox
          originalX = getPositionX(e);
          originalY = getPositionY(e);
          e.preventDefault();
        }
      }
      function onTouchEnd(e) {
        swipeFlag = false;
      }
      //横方向の座標を取得
      function getPositionX(e) {
        return e.originalEvent.touches[0].pageX;
      }
      //縦方向の座標を取得
      function getPositionY(e) {
        return e.originalEvent.touches[0].pageY;
      }
      // スワイプ処理のviewBoxの値を取得
      function getSwipeViewBox(x: number, y: number): any {
        var viewBoxValues = getPresentViewBox();
        var viewBoxVals =[];
        var ratioW = getPresentRatioW(viewBoxValues); // 拡大前の 画像width/表示窓width
        var ratioH = getPresentRatioH(viewBoxValues);  // 拡大前の 画像height/表示窓height

        viewBoxVals[0] = parseFloat(viewBoxValues[0]);		// Convert string 'numeric' values to actual numeric values.
        viewBoxVals[1] = parseFloat(viewBoxValues[1]);
        viewBoxVals[2] = parseFloat(viewBoxValues[2]);
        viewBoxVals[3] = parseFloat(viewBoxValues[3]);

        viewBoxVals[0] += (x * ratioW);
        viewBoxVals[1] += (y * ratioH);
        return viewBoxVals;
      }
    });
    // ドラッグ操作（移動）
    $(function() {
      $(document).find('#venue').on('mousedown', onDragStart); //マウスが触れたか検知
      $(document).find('#venue').on('mousemove', onDragMove); //マウスが動いたか検知
      $(document).find('#venue').on('mouseup', onDragEnd); //マウスが動いたか検知
      var originalX, originalY, x, y, viewBoxVals;
      var dragFlag = false;
      //ドラッグ開始時の座標を格納
      function onDragStart(e) {
          dragFlag = true;
          originalX = getPositionX(e);
          originalY = getPositionY(e);
      }
      //ドラッグの距離を取得
      function onDragMove(e) {
        if (dragFlag) {
          x = originalX - getPositionX(e);
          y = originalY - getPositionY(e);
          viewBoxVals = getDragViewBox(x, y);
          $("#venue").children().attr('viewBox', viewBoxVals.join(' ')); // set the viewBox
          originalX = getPositionX(e);
          originalY = getPositionY(e);
          e.preventDefault();
        }
      }
      function onDragEnd(e) {
        dragFlag = false;
      }
      //横方向の座標を取得
      function getPositionX(e) {
        return e.offsetX;
      }
      //縦方向の座標を取得
      function getPositionY(e) {
        return e.offsetY;
      }
      // スワイプ処理のviewBoxの値を取得
      function getDragViewBox(x: number, y: number): any {
        var viewBoxValues = getPresentViewBox();
        var viewBoxVals =[];
        var ratioW = getPresentRatioW(viewBoxValues); // 拡大前の 画像width/表示窓width
        var ratioH = getPresentRatioH(viewBoxValues);  // 拡大前の 画像height/表示窓height

        viewBoxVals[0] = parseFloat(viewBoxValues[0]);		// Convert string 'numeric' values to actual numeric values.
        viewBoxVals[1] = parseFloat(viewBoxValues[1]);
        viewBoxVals[2] = parseFloat(viewBoxValues[2]);
        viewBoxVals[3] = parseFloat(viewBoxValues[3]);

        viewBoxVals[0] += (x * ratioW);
        viewBoxVals[1] += (y * ratioH);
        return viewBoxVals;
      }
    });
    // キー操作後のviewBoxの値を取得
    function getPanViewBox(e: any, panRate: number): any {
      var leftArrow   = 37;	// The numeric code for the left arrow key.
      var upArrow     = 38;
      var rightArrow	= 39;
      var downArrow   = 40; 
      var viewBoxValues = getPresentViewBox();
      var viewBoxVals =[];

      viewBoxVals[0] = parseFloat(viewBoxValues[0]);		// Convert string 'numeric' values to actual numeric values.
      viewBoxVals[1] = parseFloat(viewBoxValues[1]);
      viewBoxVals[2] = parseFloat(viewBoxValues[2]);
      viewBoxVals[3] = parseFloat(viewBoxValues[3]);
      
      switch (e.keyCode)
      {
        case leftArrow:
          viewBoxVals[0] += panRate;	// Increase the x-coordinate value of the viewBox attribute to pan right.
          break;
        case rightArrow:
          viewBoxVals[0] -= panRate;	// Decrease the x-coordinate value of the viewBox attribute to pan left.
          break;
        case upArrow:
          viewBoxVals[1] += panRate;	// Increase the y-coordinate value of the viewBox attribute to pan down.
          break;
        case downArrow:
          viewBoxVals[1] -= panRate;	// Decrease the y-coordinate value of the viewBox attribute to pan up.      
          break;                         
      } 
      return viewBoxVals;
    }
    //ピンチ操作

  }
  // ダイアログの表示
  showDialog() {
    this.displayDetail = true;

  }
  // ダイアログの消去
  removeDialog() {
    let i: number;
    this.displayDetail = false;
    //this.ticketDetail = false;
    $('#'+this.selectedSeatId, '#venue').css({
            'fill': this.selectedSeatColor});
    if (this.fromAddList) {            
      for (i=0; i < this.countSelect; i++) {
        if (this.selectedSeatList[i]==this.selectedSeatId) {
          this.selectedSeatList.splice(i, 1);
          this.countSelect--;
        }
      } 
    }
    if (this.countSelect == 0) {
      this.ticketDetail = false;
    }
  }
  // リストへの追加
  addSeatList() {
    this.displayDetail = false;
    this.ticketDetail = true;
    this.fromAddList = true;
    this.selectedSeatList[this.countSelect] = this.selectedSeatId;
    this.countSelect++;
  }
}



