/*以下のAPIを呼び出し情報の取得管理を行うサービス（Injectable）。
・席種情報検索　　http://dev.altair-spa.tk/cart_api/api/v1/performances/{performanceId}/stock_types
・席種情報　　　　http://dev.altair-spa.tk/cart_api/api/v1/performances/{performanceId}/stock_types/{stockTypeId}
*/
import { Injectable, EventEmitter } from '@angular/core';
import { XHRBackend, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { ApiBase }  from './api-base.service';
import { ISuccessResponse, IErrorResponse, IStockTypeResponse, IStockTypesResponse, IStockType}  from './interfaces';
import { ApiConst } from '../../app.constants';

@Injectable()
export class StockTypesService extends ApiBase{
    
    public stockTypeLoaded$: EventEmitter<IStockType>;
    private stockTypes: IStockType;

    constructor(backend: XHRBackend,
              options: RequestOptions) {
    super(backend, options);
    this.stockTypeLoaded$ = new EventEmitter<IStockType>();
    }

    /**
   * 席種情報取得
   * @param {number} performanceId 公演ID
   * @return {Observable} see http.get()
   */
  getStockType(performanceId: number,stockTypeId: number): Observable<IStockTypeResponse | IErrorResponse> {
    const url = `${ApiConst.API_URL.STOCK_TYPE.replace(/{:performance_id}/, performanceId + '')
                .replace(/{:stock_type_id}/, stockTypeId + '')}`;
    var get = this.httpGet(url);
    get.subscribe((response: IStockTypeResponse) => {
        this.stockTypes = response.data.stock_type;
        this.stockTypeLoaded$.emit(this.stockTypes);
    });
    return get;
  }
  /**
   * 席種情報検索
   * @param  {number}     performanceId 公演ID
   * @return {Observable} see http.get()
   */
  findStockTypesByPerformanceId(performanceId: number): Observable<IStockTypesResponse | IErrorResponse> {
    const url = `${ApiConst.API_URL.STOCK_TYPES.replace(/{:performance_id}/, performanceId + '')}`;
    var get = this.httpGet(url);
    get.subscribe((response: IStockTypesResponse) => {
    });
    return this.httpGet(url);
  }
}



